@import './../../assets/styles/variables';



.products__item {
    width: calc(100%/5 - 54px);

    border-radius: 5px;
    box-shadow: $_box_shadow;
    background-color: $base;

    margin: 0 22px 44px ;
    padding: 5px;

    border-radius: 15px;

    position: relative;
}


.products__photo_wrapper {
    width: 100%;

    position: relative;
    z-index: 0;

    overflow: hidden;

    border-radius: 15px;
}


.products__photo {
    width: 100%;

    object-fit: cover;
    overflow: hidden;

    border-radius: 15px;
    max-height: 330px;
    height: auto;
    filter: brightness(0.8);
}


.products__photo--placeholder {
    opacity: 0;
}


.products__description {
    padding: 20rem 8rem;

    font-weight: 400;

    position: relative;
}


.products__price--placeholder {
    font-size: 32rem;
    margin-bottom: 10rem;
    height: 40px;
    width: 50%;
    background: $gray;
    border-radius: 20px;
}


.products__name--placeholder {
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;

    font-size: 20rem;

    font-weight: 200;

    margin-bottom: 15rem;
    height: 30px;
    width: 40%;
    background: $gray;
    border-radius: 20px;
}   


.products__icons--placeholder {
    position: absolute;

    top: 20rem;
    right: 8rem;

    display: flex;
    column-gap: 10rem;

    z-index: 0;

    height: 30px;
    width: 40%;
    background: $gray;
    border-radius: 20px;
}


.products__icons--placeholder, .products__price--placeholder, .products__name--placeholder,
.products__photo_wrapper--placeholder {
    background: linear-gradient(
      100deg,
      rgba(255, 255, 255, 0) 40%,
      rgba(255, 255, 255, .5) 50%,
      rgba(255, 255, 255, 0) 60%
    ) $gray;
    background-size: 200% 100%;
    background-position-x: 180%;
    animation: 1s loading ease-in-out infinite;
  }
  
  @keyframes loading {
    to {
      background-position-x: -20%;
    }
  }




@media (min-width: 1920px) {
    .products__icons {
        position: absolute;
    
        top: 20px;
        right: 8px;
    
        display: flex;
        column-gap: 10px;
    
        z-index: 0;
    }
}


@media (max-width: 1440px) {
    .products__item {
        width: calc(100%/5 - 32px);
    
        border-radius: 5px;
        box-shadow: $_box_shadow;
        background-color: $base;
    
        margin: 0 11px 44px ;
        padding: 5px;
    
        border-radius: 15px;
    }

    .products {
        display: flex;
        flex-wrap: wrap;
    
        margin: 0 -11px;
        
    }
}


@media (max-width: 1280px) {
    .products__item {
        width: calc(100%/4 - 32px);
    
        border-radius: 5px;
        box-shadow: $_box_shadow;
        background-color: $base;
    
        margin: 0 11px 44px ;
        padding: 5px;
    
        border-radius: 15px;
    }


    .products {
        display: flex;
        flex-wrap: wrap;
    
        margin: 0 -11px;
        
    }
}


@media (max-width: 1024px) {
    .products__item {
        width: calc(100%/3 - 32px);
    
        border-radius: 5px;
        box-shadow: $_box_shadow;
        background-color: $base;
    
        margin: 0 11px 44px ;
        padding: 5px;
    
        border-radius: 15px;
    }

    .products {
        display: flex;
        flex-wrap: wrap;
    
        margin: 0 -11px;
        
    }
}


@media (max-width: 768px) {
    .products__item {
        width: calc(100%/3 - 20px);
    
        border-radius: 5px;
        box-shadow: $_box_shadow;
        background-color: $base;
    
        margin: 0 5px 44px ;
        padding: 5px;
    
        border-radius: 15px;
    }

    .products {
        display: flex;
        flex-wrap: wrap;
    
        margin: 0 -5px;
    }

    .products__icons {
        top: unset;
    }
}


@media (max-width: 600px) {
    .products__item {
        width: calc(100%/2 - 10px);
    
        border-radius: 5px;
        box-shadow: $_box_shadow;
        background-color: $base;
    
        margin: 0 2px 15px ;
        padding: 3px;
    
        border-radius: 15px;
        position: relative;
    }

    .products {
        display: flex;
        flex-wrap: wrap;
    
        margin: 0 -5px;
    }

    .products__photo {
        border-radius: 7px;
    }
}
