@import './../../assets/styles/variables';





.overlay {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, 0.5);
    backdrop-filter: blur(2px);
    z-index: 999;

    cursor: pointer;
}


.overlay--hidden {
    display: none;
}