@import './../../assets/styles/variables';



.preview_loading  {
    position: relative;
    width: 100%;
    height: 350px;
    border-radius: 15px;
    overflow: hidden;
}
  
.skeleton_loader {
    background: linear-gradient(
      100deg,
      rgba(255, 255, 255, 0) 40%,
      rgba(255, 255, 255, .5) 50%,
      rgba(255, 255, 255, 0) 60%
    ) $gray;
    background-size: 200% 100%;
    background-position-x: 180%;
    animation: 1s loading ease-in-out infinite;
  }
  
  @keyframes loading {
    to {
      background-position-x: -20%;
    }
  }
  
  


.preview__wrapper {
    margin-top: 40px;
    width: 100%;

    display: flex;
    overflow: hidden;
}


.preview__photo-big,
.preview__photo-medium,
.preview__photo-small {
    width: 100%;

    border-radius: 15px;
    cursor: pointer;
}

.preview__photo-medium,
.preview__photo-small {
    display: none;
}



.preview__pagination {
    display: flex;
    justify-content: center;

    align-items: center;

    column-gap: 25px;
    margin-top: 25px;
}


.preview__pagination-item {
    width: 10px;
    height: 10px;

    background: rgba($accent, 0.6);
    border-radius: 50%;

    cursor: pointer;
}


.preview__pagination-item-current {
    background: rgba($accent, 1);
    width: 15px;
    height: 15px;
}



@media (max-width: 1024px) {
    .preview__photo-big {
        display: none;
    }

    .preview__photo-medium {
        display: block;
    }
}


@media (max-width: 600px) {
    .preview__photo-medium {
        display: none;
    }

    .preview__photo-small {
        display: block;
    }
}


@media (max-width: 768px) {

    .preview__pagination {
        margin-top: 15px;
    }
}


/* Transition group */
.fade-enter {
    opacity: 0;
    transition: opacity 0.5s ease-in-out;
}

.fade-enter-active {
    opacity: 1;
}

.fade-exit {
    opacity: 1;
    transition: opacity 0.5s ease-in-out;
}

.fade-exit-active {
    opacity: 0;
    display: none;
}
/* ./Transition group */